<template>
  <div class="c-main wow fadeIn" style="">
    <el-row class="topArea wow fadeInDown" data-wow-delay="0.5s">
      <el-col :span="24">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>

    <div class="mainBody">

      <el-row class="headArea wow fadeInDown panelArea" data-wow-delay="0.3s">
        <el-col :span="24">
          <span class="title"></span>
          <span class="description"> </span>
        </el-col>
      </el-row>

      <el-row class="panelArea">
        <el-col :span="12">
          <div>
            <h5></h5>

            <el-input
              type="textarea"
              :rows="10"
              placeholder=""
              v-model.trim="textarea">
            </el-input>

            <el-row class="panelArea">
              <el-col :span="8">

                <el-input
                  placeholder=""
                  v-model.trim="replace"
                  clearable>
                </el-input>

              </el-col>
              <el-col :span="8">
                <span class="replace-tips">  **</span>
              </el-col>

            </el-row>

            <el-button @click="saveFilter()" size="small" type="primary"></el-button>

          </div>

        </el-col>
        <el-col :span="12">
          <div class="tips">

            <el-alert
              title="1,2,3   "
              type="info">
            </el-alert>

          </div>
        </el-col>
      </el-row>


    </div>


  </div>
</template>

<script>
  export default {
    name: "commentManagement",
    data() {
      return {
        textarea: "",
        replace: ""
      }
    },
    methods: {

      getFilter() {


        this.requestApiFnc("/newsDiscuss/getFilter", "get", null ,(res)=>{
          const {data:{code, map:{discussFilter:{keyword,replaceWord}},message}} = res;
          if(code===200){
            this.textarea = keyword;
            this.replace = replaceWord;
          }else {
            this.ele_alert(message,"error")
          }
        })

      },
      saveFilter() {

        let keyword = [];
        let keywordString="";
        let replaceWord = this.replace;
        if (this.textarea) {
          keyword = this.textarea.split(",");
          if (keyword[keyword.length-1]===""){
            keyword.splice(keyword.length-1,1);
          }
          //   
          keywordString = keyword.join(",")
        }else {
          keywordString = "";
        }
        this.requestApiFnc("/newsDiscuss/saveFilter", "post", {
          keyword:keywordString,
          replaceWord
        },(res)=>{
          const {data:{code,map,message,success}} =res;
          if(code===200){
            this.ele_alert("","success");
          }else {
            this.ele_alert(message,"error")
          }
        })

      }
    },created(){
      this.getFilter();
    }
  }
</script>

<style lang="scss" scoped>

  .c-main {
    .tips {
      padding: 15px;
      padding-top: 60px;
    }

    .replace-tips {
      font-size: 12px;
      color: grey;
      box-sizing: border-box;
      padding: 15px;
    }

  }
</style>



