<template>
  <div class="c-main wow fadeIn" style="">
    <el-row class="topArea wow fadeInDown" data-wow-delay="0.5s">
      <el-col :span="24">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <div class="mainBody">

      <el-row class="headArea wow fadeInDown panelArea" data-wow-delay="0.3s">
        <el-col :span="24">
          <span class="title"></span>
          <span class="description"></span>
        </el-col>
      </el-row>


      <el-row class="panelArea">

        <el-col :span="24">
          <el-form ref="form" :model="form" label-width="100px">
            <el-form-item label="LOGO">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img v-if="form.imageUrl" :src="form.imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="" >
              <el-input v-model="form.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input  v-model="form.keyword" placeholder=" SEO"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input  v-model="form.keyword" placeholder="slogan  "></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="form.desc" placeholder=" SEO"></el-input>
            </el-form-item>
            <!--<el-form-item label="">-->
              <!--<el-select v-model="form.region" placeholder="">-->
                <!--<el-option label="" value="shanghai"></el-option>-->
                <!--<el-option label="" value="beijing"></el-option>-->
              <!--</el-select>-->
            <!--</el-form-item>-->
            <el-form-item label="">
              <el-switch v-model="form.open"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="form.open"></el-switch>
            </el-form-item>
            <el-form-item label="">
              <el-switch v-model="form.open"></el-switch>
            </el-form-item>


            <el-form-item>
              <el-button type="primary" @click="onSubmit"></el-button>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>




    </div>
  </div>
</template>

<script>
    export default {
        name: "config",
      data(){
        return {
          form: {
            name: '',
            keyword:"",
            desc: '',
            region: '',
            date1: '',
            date2: '',
            open: false,
            imageUrl: ''
          }
        }
      },method:{
        onSubmit() {
          console.log('submit!');
        },
        handleAvatarSuccess(res, file) {
          this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isJPG) {
            this.$message.error(' JPG !');
          }
          if (!isLt2M) {
            this.$message.error(' 2MB!');
          }
          return isJPG && isLt2M;
        }
      }
    }
</script>

<style scoped>

</style>



