<template>
  <div class="c-main wow fadeIn">

    <el-row class="topArea wow fadeInDown" data-wow-delay="0.5s">
      <el-col :span="24">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

      </el-col>
    </el-row>

    <div class="mainBody">

      <el-row class="headArea wow fadeInDown panelArea" data-wow-delay="0.3s">
        <el-col :span="24">
          <span class="title"></span>
          <span class="description"></span>
        </el-col>
      </el-row>


      <el-row class="panelArea ">

        <el-col :span="8" :offset="12" :md="8" :lg="8" :xs="24" :sm="24">
          <el-input placeholder="" v-model="search" class="input-with-select" size="small">
            <el-select v-model="search_select" slot="prepend" placeholder="........">
              <!--<el-option label="ID" value="id"></el-option>-->
              <el-option label="" value="userName"></el-option>
              <el-option label="" value="realName"></el-option>
              <el-option label="" value="phone"></el-option>
              <el-option label="" value="email"></el-option>
              <el-option label="QQ" value="qq"></el-option>
            </el-select>
            <el-button slot="append" @click="requestApi('search')" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4" :md="4" :lg="4" :xs="24" :sm="24"
                style="text-align: left;box-sizing: border-box;padding-left: 25px">
          <el-tooltip content="" placement="top">
            <el-button type="primary" icon="el-icon-refresh" @click="refresh" size="small"
                       plain></el-button>
          </el-tooltip>

          <el-tooltip content="" placement="top">
            <el-button type="primary" @click="openDialog('add')" icon="el-icon-plus" size="small" plain></el-button>
          </el-tooltip>

        </el-col>


      </el-row>

      <el-row class="panelArea">
        <!-- :header-cell-style="{background:' #33a0d7',color:'white'}"  -->
        <el-col :span="24">
          <!--v-loading="loading"-->
          <el-table
            :data="tableData"
            border
            style="width: 100%">
            <el-table-column
             width="50"
             type="index"
              label="#">
            </el-table-column>
            <el-table-column

              prop="userName"
              label="">
            </el-table-column>
            <el-table-column

              prop="realName"
              width="120"
              label="">
            </el-table-column>
            <el-table-column

              prop="gender"
              width="50"
              label="">
            </el-table-column>
            <el-table-column

              prop="role"
              width="120"
              label="">
            </el-table-column>
            <el-table-column

              prop="status"
              width="120"
              label="">
            </el-table-column>
            <el-table-column

              width="150"
              prop="phone"
              label="">
            </el-table-column>
            <el-table-column

              prop="email"
              label="">
            </el-table-column>
            <el-table-column

              prop="userQq"
              width="150"
              label="QQ">
            </el-table-column>

            <el-table-column

              prop="createDate"
              width="160"
              label="">
            </el-table-column>
            <el-table-column

              prop="loginTime"
              width="160"
              label="">
            </el-table-column>

            <el-table-column
              align="center"
              fixed="right"
              label=""
              width="100">

              <template slot-scope="scope">
                <el-button @click="openDialog('edit',scope.row)" type="text" size="small"></el-button>

                <el-button type="text" size="small" @click="openDialog('delete',scope.row)"></el-button>
              </template>
            </el-table-column>

          </el-table>


        </el-col>


      </el-row>

      <el-row class="panelArea">
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40,50]"
            :page-size="page_size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="page_total">
          </el-pagination>
        </div>


      </el-row>

    </div>


    <el-dialog
      :append-to-body="true"
      title=""
      :visible.sync="dialogVisible"
      width="30%">
      <span>{{this.dialogText}}</span>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogConfirm('cancel')"> </el-button>
    <el-button type="primary" @click="dialogConfirm('confirm')"> </el-button>
  </span>
    </el-dialog>


    <el-dialog :append-to-body="true" :title="this.dialogText" @close="closeUserDialog" :visible.sync="dialogFormNew">
      <el-form :model="form_user" ref="userForm" :rules="formRulers" size="small">
        <el-form-item label="" :label-width="formLabelWidth" prop="userName">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.userName" auto-complete="off" placeholder=""></el-input>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.realName" auto-complete="off"></el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item v-if="this.action==='add'" label="" prop="password" :label-width="formLabelWidth">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.password" type="password" auto-complete="off" placeholder=""></el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item v-if="this.action==='edit'" label="" :label-width="formLabelWidth">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.password" type="password" auto-complete="off"
                        placeholder=""></el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio-group v-model="form_user.gender">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form_user.role" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form_user.status" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth" prop="phone">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.phone" auto-complete="off" placeholder=""></el-input>
            </el-col>
          </el-row>
        </el-form-item>


        <el-form-item label="" :label-width="formLabelWidth" prop="email">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.email" auto-complete="off" placeholder=""></el-input>
            </el-col>
          </el-row>
        </el-form-item>

        <el-form-item label="QQ" :label-width="formLabelWidth">
          <el-row>
            <el-col :span="12">
              <el-input v-model="form_user.userQq" auto-complete="off"></el-input>
            </el-col>
          </el-row>
        </el-form-item>


      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogConfirm('cancel')"> </el-button>
        <el-button type="primary" @click="dialogConfirm('confirm')"> </el-button>
      </div>
    </el-dialog>

  </div>

</template>

<script>
  export default {
    name: "userManagement",
    data() {
      let checkUserName = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (value.length < 3 || value.length > 15) {
          return callback(new Error('3-15'));
        }
        this.requestApi('checkName', function (v) {
          if (!v) {
            return callback(new Error(''));
          } else {
            callback(); //
          }
        });
      };
      let checkPhone = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (value.length !== 11 || isNaN(value)) {
          return callback(new Error(''));
        }
        this.requestApi('checkPhone', function (v) {
          if (!v) {
            return callback(new Error(''));
          } else {
            callback(); //
          }
        });

      };
      let checkEmail = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (value.indexOf("@") === -1) {
          return callback(new Error(''));
        }
        this.requestApi('checkEmail', function (v) {
          if (!v) {
            return callback(new Error(''));
          } else {
            callback(); //
          }
        });
      };

      let checkPassword = (rule, value, callback) => {
        if (!value) {
          return callback(new Error(''));
        }
        if (value.length <= 5) {
          return callback(new Error(""));
        } else {
          callback(); //
        }
      };
      return {
        loading: true,
        action: "",//
        currentPage: 1,//
        page_total: 0,// 
        page_size: 10,//  
        userID: "",//id 
        dialogVisible: false,//   
        dialogFormNew: false,//       
        formLabelWidth: "80px",//label
        formRulers: {
          userName: [
            {validator: checkUserName, trigger: 'blur'},
          ],
          phone: [
            {validator: checkPhone, trigger: 'blur'}
          ],
          email: [
            {validator: checkEmail, trigger: 'blur'}
          ],
          password: [
            {validator: checkPassword, trigger: 'blur'}
          ]
        },

        fromCheck1: {
          userName: false,
          phone: false,
          email: false,
        },
        form_user: {
          userName: "",
          gender: "",
          realName: "",
          role: "",
          status: "1",
          phone: "",
          email: "",
          password: "",
          userQq: "",
        },//   
        dialogText: "",
        search: "",//
        search_select: "userName",//
        tableData: [],//

      }
    },
    methods: {
      /**
       *  
       * @param val
       */
      handleSizeChange(val) {
        this.page_size = val;
        this.currentPage = 1;


          this.requestApi("getUser");

      },
      /**
       * 
       * @param val
       */
      handleCurrentChange(val) {
        this.currentPage = val;

          this.requestApi("getUser");

      },
      handleClick(row) {
        console.log(row);
      },
      /**
       *     
       * @param type
       * @param data
       */
      openDialog(type, data) {

        if (type === 'delete') {

          this.action = "delete";
          this.userID = data.id;
          this.dialogVisible = true;
          this.dialogText = ""

        } else if (type === 'add') {
          this.userID = "";
          this.action = "add";
          this.dialogText = "";
          this.dialogFormNew = true;
          this.form_user = {
            userName: "",
            gender: "",
            realName: "",
            role: "",
            status: "1",
            phone: "",
            email: "",
            password: "",
            userQq: "",
          }

        } else if (type === 'edit') {
          this.action = "edit";
          this.userID = data["id"];
          for (let item in data) {

            if (data.hasOwnProperty(item)) {
              this.form_user[item] = data[item];
            }

          }

          if (this.form_user.status === "") {
            this.form_user.status = "1"
          } else if (this.form_user.status === "") {
            this.form_user.status = "0"
          }
          this.form_user.password = "";


          this.dialogText = "";
          this.dialogFormNew = true;

        }


      },


      /**
       *  
       */
      closeUserDialog() {
        this.$refs['userForm'].clearValidate();//
      },


      /**
       * 
       * @param res
       */
      dialogConfirm(res) {


        if (this.action === "delete") {

          if (res === "cancel") {
            this.dialogVisible = false;
            this.action = "";

          } else {

            console.log("", this.userID);

            this.requestApi("delete");

            this.dialogVisible = false;
            this.action = "";


          }


        } else if (this.action === "add") {

          if (res === "cancel") {
            this.dialogFormNew = false;
            this.action = "";

          } else {

            console.log("");

            this.$refs["userForm"].validate((valid) => {
              if (valid) {
                this.requestApi("add");
                this.dialogFormNew = false;
              } else {
                console.log('error submit!!');
                return false;
              }
            });

          }


        } else if (this.action === "edit") {

          if (res === "cancel") {
            this.dialogFormNew = false;
          } else {
            console.log("");

            this.$refs["userForm"].validate((valid) => {
              if (valid) {
                this.requestApi("edit");
                this.dialogFormNew = false;
                console.log("success");
                this.requestApi("getUser");
              } else {
                console.log('error submit!!');
                return false;
              }
            });


          }


        }


      },


      refresh(){

        this.search="";
        this.requestApi("getUser");
      },


      requestApi(action, verifyCB) {

        switch (action) {

          case "add":
            this.$axios.post("/user/register", {
              userName: this.form_user.userName,
              password: this.form_user.password,
              realName: this.form_user.realName,
              gender: this.form_user.gender,
              phone: this.form_user.phone,
              userImg: "1",
              userQq: this.form_user.userQq,
              email: this.form_user.email,
              status: this.form_user.status,
            }).then((res) => {

              if (res.data.code === 200) {

                this.tips( res.data.message,"success")
                this.requestApi("getUser");
              } else {
                this.tips( res.data.message,"warning")

              }
            }).catch(
              (error) => {
                console.log(error);
              }
            );
            break;
          case "edit":

            this.$axios.put("/user/update", {
              id: this.userID,
              userName: this.form_user.userName,
              password: this.form_user.password,
              realName: this.form_user.realName,
              gender: this.form_user.gender,
              phone: this.form_user.phone,
              userImg: "1",
              userQq: this.form_user.userQq,
              email: this.form_user.email,
              status: this.form_user.status,

            }).then((res) => {
              if (res.data.code === 200) {

                this.tips("","success");
                this.requestApi("getUser")
              } else {
                this.tips(res.data.message,"warning");
              }

            }).catch((error) => {
            });


            break;
          case "delete":
            this.$axios.delete("/user/delete", {
              params: {
                userId: this.userID
              }

            }).then((res) => {
              if (res.data.code === 200) {

                this.tips("","success");
                this.requestApi("getUser")
              } else {
                this.tips(res.data.message,"warning");
              }

            }).catch((error) => {

              this.tips("","error");

            });
            break;
          case "getUser":
            if (this.search !=="") {
              this.requestApi("search");//  
           return;
            }
            this.loading = true;
            // this.$axios.get("/user/getAll?pageNum=" + this.currentPage + "&pageSize=" + this.page_size).then((res) => {
            this.$axios.get("/user/getAll",{
              params:{
                pageNum:this.currentPage,
                pageSize:this.page_size,

              }
            }).then((res) => {

              if (res.data.code === 200) {

                let list = res.data.map.pageInfo.list;


                this.page_total = res.data.map.pageInfo.total;

                let _this = this;
                this.tableData = list.map(function (item) {

                  if (item.status === 1) {
                    item.status = ""
                  } else if (item.status === 0) {
                    item.status = ""
                  }
                  item.createDate = _this.showTime(item.createDate);
                  if (item.loginTime) {
                    item.loginTime = _this.showTime(item.loginTime);
                  } else {
                    item.loginTime = "";
                  }
                  return item;

                });

              } else {
                this.tips(res.data.message,"warning");
              }
              this.loading = false;
            }).catch((error) => {
              this.tips( "","error");
                console.log(error);
                this.loading = false;
              }
            );

            break;
          case "checkName":
            this.$axios.get("/user/checkName", {
              params: {
                userId: this.userID,
                userName: this.form_user.userName,
              }
            }).then((res) => {

              if (res.data.code === 200) {

                this.fromCheck1.userName = true;//

                verifyCB(this.fromCheck1.userName);//  
                this.form_user.hahah = "dd";


              } else if (res.data.code === 500) {
                this.fromCheck1.userName = false;//

                verifyCB(this.fromCheck1.userName);// 
              }

            }).catch((error) => {
              this.tips( "","error");

              console.log(error)


            });
            break;
          case "checkEmail":
            let _this = this;
            console.log(_this.form_user);
            this.$axios.get("/user/checkEmail", {
              params: {
                userId: this.userID,
                email: this.form_user.email,
              }
            }).then(function (res) {
              if (res.data.code === 200) {
                _this.fromCheck1.email = true;//
                verifyCB(_this.fromCheck1.email);//  
              } else if (res.data.code === 500) {
                _this.fromCheck1.email = false;//
                verifyCB(_this.fromCheck1.email);//  
              }

            }).catch((error) => {

              this.tips( "","error");
              console.log(error)


            });
            break;
          case "checkPhone":
            this.$axios.get("/user/checkPhone", {
              params: {
                userId: this.userID,
                phone: this.form_user.phone,
              }
            }).then((res) => {
              if (res.data.code === 200) {
                this.fromCheck1.phone = true;//
                verifyCB(this.fromCheck1.phone);//  
              } else if (res.data.code === 500) {
                this.fromCheck1.phone = false;//
                verifyCB(this.fromCheck1.phone);//

              }


            }).catch((error) => {
              this.tips( "","error");
              console.log(error);


            });
            break;
          case "search":

            if (this.search === "") {
              this.tips("","warning");
              return

            }
            this.loading = true;
            this.$axios.get("/user/queryUser", {
              params: {
                query: this.search,
                type: this.search_select,
                pageNum: this.currentPage,
                pageSize: this.page_size,

              }
            }).then((res) => {
              if (res.data.code === 200) {
                let list = res.data.map.pageInfo.list;

                if (list.length === 0) {
                  this.tips("","info");
                  this.tableData = [];
                  this.page_total = 0;
                  this.loading = false;
                  return;
                }

                this.page_total = res.data.map.pageInfo.total;
                let _this = this;
                this.tableData = list.map(function (item) {

                  if (item.status === 1) {
                    item.status = ""
                  } else if (item.status === 0) {
                    item.status = ""
                  }
                  item.createDate = _this.showTime(item.createDate);
                  if (item.loginTime) {
                    item.loginTime = _this.showTime(item.loginTime);
                  } else {
                    item.loginTime = "";
                  }
                  return item;

                });


              } else if (res.data.code === 500) {
                this.tips(res.data.message,"warning");

              }

              this.loading = false;


            }).catch((error) => {
              this.tips("","error");
              console.log(error);
              this.loading = false;

            });


            break;


        }

      },
    }, created() {
      this.requestApi("getUser");


    }
  }
</script>

<style lang="scss" scoped>
  .c-main /deep/ .el-select .el-input {
    width: 120px;
    text-align: center;
  }

  .c-main /deep/ .input-with-select .el-input-group__prepend {
    background-color: #fff;
  }


</style>



