<template>
  <div class="c-main wow fadeIn"  style="">
    <el-row class="topArea wow fadeInDown" data-wow-delay="0.5s">
      <el-col :span="24">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <div class="mainBody">

    <el-row class="headArea wow fadeInDown panelArea" data-wow-delay="0.3s">
      <el-col :span="24">
        <span class="title"></span>
        <span class="description"></span>
      </el-col>
    </el-row>


      <el-row class="panelArea ">
        <el-form :model="form" label-width="80px" ref="form">
          <el-form-item label="">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item style="position: relative">
            <el-upload
              :action="uploadUrl"
              :limit="1"
              :multiple="false"
              :on-error="uploadError"
              :on-remove="handleRemove"
              :on-success="uploadSuccess"
              list-type="picture-card">
              <i class="el-icon-plus"></i>
            </el-upload>

          </el-form-item>
          <el-form-item label="">
            <el-select placeholder="" v-model="form.type">
              <el-option :key="nt.id" :label="nt.typeName" :value="nt.id" v-for="nt in newsType"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="form.digest"></el-input>
          </el-form-item>
          <el-form-item label="">
            <mavon-editor @change="getHtmlContent"  @imgAdd="$imgAdd" @imgDel="$imgDel" ref=md style="height: 400px" v-model="form.content"></mavon-editor>
            <!--<el-input type="textarea" v-model="form.content"></el-input>-->
          </el-form-item>

          <el-form-item label="">
            <el-checkbox-group v-model="form.option">
              <el-checkbox label="" name="discuss"></el-checkbox>
              <el-checkbox label="" name="top"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item v-if="this.editModel">
            <el-button @click="updateNews(1)" size="small" type="primary"></el-button>
            <el-button @click="updateNews(0)" size="small" type="success"></el-button>
            <el-button size="small"></el-button>
          </el-form-item>
          <el-form-item v-else>
            <button @click="moni()"></button>
            <el-button @click="onSubmit(1)" size="small" type="primary"></el-button>
            <el-button @click="onSubmit(0)" size="small" type="success"></el-button>
            <el-button size="small"></el-button>
          </el-form-item>
        </el-form>

      </el-row>
    </div>

  </div>
</template>

<script>
  export default {
    name: "releaseNews",
    data() {
      return {
        uploadUrl:window.CUIT_server.API_ROOT+"/user/uploadImg",
        form: {
          id:'',//  
          title: '',
          firstImg:"",
          type: '',
          digest:"",
          content:"",
          contentHtml:"",
          option:[""],
        },
        editModel:false,
        newsType:[]

      }
    },
    methods: {

      $imgDel(pos){
        delete this.img_file[pos];
      },
      $imgAdd(pos, $file){
        // ..
        let formdata = new FormData();
        formdata.append('file', $file);
        console.log(formdata.get("file"));
        this.$axios({
          url: '/user/uploadImg',
          method: 'post',
          data: formdata,
          headers: { 'Content-Type': 'multipart/form-data' },
        }).then((res) => {
          // .url![...](0) -> ![...](url)
          /**
           * $vm mavonEditor
           * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
           * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
           */
          console.log(res);
         let url= window.CUIT_server.API_ROOT+"/"+res.data.map.url;
          this.$refs.md.$img2Url(pos, url);
        }).catch((error)=>{
          // console.log(error);
          // this.$refs.md.$img2Url(pos, "https://www.baidu.com/img/bd_logo1.png");
        })

      },

     getHtmlContent(value,render){
        this.form.contentHtml=render;
     },
      getNewsType() {
        let data = {
          pageNum: 1,
          pageSize: 100
        };
        this.requestApiFnc("/newsType/getAll", "get", data,
          (res) => {
            const {data:{code,map:{pageInfo:{list}},message,success}} =res;
            if(code !==200){
              this.ele_alert(message,"error");
              return;
            }
            this.newsType=list;
            // console.log(this.tableData);
          })
      },
      moni(){

        let da = [
          "",
          "",
          "",
          "",
          "",
          "255G",
          "",
          "Micro LED",
          " 2019 6  3 ",
          "",
          "Micro LED",
          " 2019 6  3 ",
        ]

     setInterval(()=>{

       let discuss;
       let important;

       this.form.option.indexOf("") !== -1 ? discuss = 1 : discuss = 0;
       this.form.option.indexOf("") !== -1 ? important = 1 : important = 0;

       const data={
         title:da[Math.floor(Math.random()*10)] + Math.floor(Math.random()*10)+Math.floor(Math.random()*10),
         author:"33ea7030-d38f-4d0b-8338-b2ba36f2d7ad",
         // type:"",
         type:"f38b292e-14ac-4738-b0d9-accd74f968aa",
         digest:"",
         firstImg:"http://120.78.149.155:8040/1545232360678.jpeg",
         content:"![croppedtimg.jpg](http://120.78.149.155:8040/1545232379678.jpeg)",
         contentHtml:"<p><img src='http://120.78.149.155:8040/1545232379678.jpeg' alt='croppedtimg.jpg' /></p>",
         status:1,//type 1   type 0 
         discuss,
         important
       };
       this.requestApiFnc("/news/add","post",data,(res)=>{
         let {data:{code,map,message}} = res;
         if (code===200){
           this.tips("","success");
           // this.ele_alert("","success",()=> this.$router.push({path: "/newsManagement"}));
         }else {
           this.ele_alert(message,"error");
         }
       })

     },1500)

      },
      onSubmit(type) {
        // console.log(this.$refs.md);

        let discuss;
        let important;

 this.form.option.indexOf("") !== -1 ? discuss = 1 : discuss = 0;
 this.form.option.indexOf("") !== -1 ? important = 1 : important = 0;

       const data={
         title:this.form.title,
         author:"3f3ff415-b27f-46cd-bf4d-17b8b14836a8",
         // type:"",
         type:this.form.type,
         digest:this.form.digest,
         firstImg:this.form.firstImg,
         content:this.form.content,
         contentHtml:this.form.contentHtml,
         status:type,//type 1   type 0 
         discuss,
         important
       };


       this.requestApiFnc("/news/add","post",data,(res)=>{
         let {data:{code,map,message}} = res;
         if (code===200){
           this.ele_alert("","success",()=> this.$router.push({path: "/newsManagement"}));
         }else {
           this.ele_alert(message,"error");
         }
       })
      },
      updateNews(type) {
        // console.log(this.$refs.md);
        let discuss;
        let important;
        this.form.option.indexOf("") !== -1 ? discuss = 1 : discuss = 0;
        this.form.option.indexOf("") !== -1 ? important = 1 : important = 0;
        const data={
          id:this.form.id,
          title:this.form.title,
          author:"3f3ff415-b27f-46cd-bf4d-17b8b14836a8",
          type:this.form.type,
          digest:this.form.digest,
          firstImg:this.form.firstImg,
          content:this.form.content,
          contentHtml:this.form.contentHtml,
          status:type,//type 1   type 0 
          discuss,
          important
        };


        this.requestApiFnc("/news/update","put",data,(res)=>{
          let {data:{code,map,message}} = res;
          if (code===200){
            this.ele_alert("","success",()=> this.$router.push({path: "/newsManagement"}));
          }else {
            this.ele_alert(message,"error");
          }
        })
      },

      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      uploadSuccess(response, file, fileList){
        console.log(response,file,fileList);
        this.form.firstImg = window.CUIT_server.API_ROOT+"/"+response.map.url;
        console.log(this.form.firstImg);
      },
      uploadError(err, file, fileList){
        console.log(err,file,fileList)
      },
      initEditModel(nid){

        this.requestApiFnc(`/news/getnewsById`,`get`,{
          newsId:nid
        },(res)=>{
          console.log(res);
          const {data:{code,map:{news},message}} = res;
          if (code===200){
            this.form.id = news.id;
            this.form.title=news.title;
            this.form.digest=news.digest;
            this.form.content=news.content;
            this.form.type=news.type;
            this.form.firstImg=news.firstImg;
            this.form.option=[];
            if(news.discuss){
              this.form.option.push("");
            }
            if (news.important) {
              this.form.option.push("");
            }
          }
          else {
            this.ele_alert(message,"error",()=> this.$router.push({path: "/newsManagement"}),()=> this.$router.push({path: "/newsManagement"}));
          }


        })

      }

    },
    mounted() {

    },
    created() {
      this.toTop();
      this. getNewsType();
      const action = this.$route.query.action;
      const newsId = this.$route.query.newsId;
      if(action && newsId){
        this.editModel = action === "edit";// action edit  
        if(this.editModel){
          this.initEditModel(newsId);
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/styles/base";

  .c-main {



  }
</style>



