import {globalLoading} from "./gloablLoading";
import axios from 'axios'
import {Base64} from 'js-base64';
import router from './router'

let baseURL = window.CUIT_server.API_ROOT;
let timeout = window.CUIT_server.TIMEOUT;

let http = axios.create({
  baseURL,
  timeout,
  headers: {
    "Content-Type": "application/json"
  }
});


// 
http.interceptors.request.use(function (config) {

  // showFullScreenLoading()
  globalLoading.showFullScreenLoading();

  console.log(config);

  let token = localStorage.getItem("login");  //token
  if (token) {
    config.headers.Authorization = Base64.decode(JSON.parse(token));   //token
  } else {

    //
    if (config.url.indexOf("login") !== -1) {
      console.log("");
      router.push({path: "/login"});

    } else {

      console.log(" ")

    }

  }


  // 
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});

// 
http.interceptors.response.use(function (response) {
  // globalLoading.tryHideFullScreenLoading();
  setTimeout(() => {
    // tryHideFullScreenLoading();
    globalLoading.tryHideFullScreenLoading();
  }, 300);

  // 
  return response;
}, function (error) {
  // 

  globalLoading.tryHideFullScreenLoading();

  // setTimeout(()=>{
  //   // tryHideFullScreenLoading();
  //   globalLoading.tryHideFullScreenLoading();
  // },300);
  return Promise.reject(error);
});


export let axiosHttp = http;



